<template>
    <div class="mask" @click.self="upt">
        <div class="content" v-if="!isEdit">
            <img :src="src" alt="">
            <h2>{{ title }}</h2>
            <p>{{ info }}</p>
            <div class="bot">
                <button @click="isEdit = !isEdit">
                    修改
                </button>
                <button @click="del">
                    删除
                </button>
            </div>
        </div>
        <div  class="content" v-else>
            <img :src="src" alt="">
            <h2><input v-model="titles" type="text"></h2>
            <p>
                <textarea rows="5" v-model="infos">

                </textarea>
            </p>
            <div class="bot">
                <button @click="save">
                    保存
                </button>
            </div>
        </div>
    </div>
</template>

<script>
export default {
    props: ["src" ,"isShow","title","info","id",],
    data() {
        return {
            isEdit:false,
            titles:this.title,
            infos:this.info,
        };
    },
    created() {

    },
    methods: {
        del(){
            this.$emit("del",this.id);
        },
        upt(){
            this.$emit("update:isShow",false)
        },
        save(){
            this.$emit("updateInfo",this.id,this.titles,this.infos);
            this.isEdit = !this.isEdit;
            this.$emit("update:isShow",false)
        }
    },
};
</script>

<style scoped lang="scss">
.mask{
    .content{
        width: 300px;
        background: #fff;
        border-radius: 8px;
        min-height: 40px;
        display: flex;
        flex-direction: column;
        align-items: center;
        padding: 20px;
    }
    h2{
        width: 100%;
        text-align: center;
    }
    p{
        width: 100%;
    }
    h2,p,div{
        padding: 10px 0;
        // flex: 1;
        button{
            margin: 0 10px;
        }
        input,textarea{
            display: block;
            width: 100%;
        }
    }
}
</style>
