<template>
  <div id="app">
    <ul>
      <li v-for="item in goodsList" :key="item.id">
          <div class="top">
              <img :src="item.imgUrl" alt="">
          </div>
          <div class="cen">
              <div>
                  {{ item.title }}
              </div>
          </div>
          <div class="bot">
              <button @click="getInfo(item)">
                  详情
              </button>
          </div>
      </li>
    </ul>
    <masks v-if="isShow" :is-show.sync="isShow" 
      :src="src" 
      :title.sync="title" 
      :info.sync="info"
      :id="id"
      @updateInfo="upt"
      @del="del"
    ></masks>
  </div>
</template>
<script>
import Mock from "mockjs";
import Vue from "vue";
import masks from "@/components/mask.vue"
Vue.directive("focus", {
    inserted(el){
        el.focus();
    }
})
var mocks = Mock.Random;
var img = mocks.dataImage('200x100');
var goodsList = [];
for(var i = 0 ; i< 5;i++){
    let obj = {};
    obj.id = mocks.guid();
    obj.imgUrl = mocks.dataImage('200x100');
    obj.title = mocks.ctitle();
    obj.info = mocks.cparagraph();
    goodsList.push(obj);
}
export default {
  components:{
    masks
  },
  data () {
    return {
      goodsList,
      src:"",
      title:"",
      info:"",
      id:"",
      isShow:false
    }
  },
  methods:{
    del(id){
      var index = this.goodsList.findIndex(ele => ele.id == id);
      this.goodsList.splice(index,1);
      this.isShow = false;
    },
    upt(id,title,info){
      var item = this.goodsList.find(ele => ele.id == id);
      item.title = title;
      item.info = info;
    },
    getInfo(val){
      this.src = val.imgUrl;
      this.title = val.title;
      this.info = val.info;
      this.id = val.id;
      this.isShow = !this.isShow
    }
  }
}
</script>
