            margin: 0;
            padding: 0;
            list-style: none;
        }

        .clearfix:after {/* 给浮动元素的父级元素加上class='clearfix' */
            content: "";
            display: block;
            height: 0;
            clear: both;
        }
        .clearfix {
            zoom: 1;/* 在IE6/7中,利用zoom:1触发haslayout属性 */
        }
        ul{
           width: 900px;
           margin: 50px auto;
        }
        li{
            border: 1px solid red;
            width: 200px;
            float: left;
            margin: 20px;
        }
        .mask{
            position: fixed;
            display: flex;
            width: 100%;
            height: 100%;
            align-items: center;
            justify-content: center;
            background: rgba(0,0,0,0.6);
            top: 0;
            left: 0;
        }
        .mask img{
            width: 80%;
        }
</style>
